import { Container } from "react-bootstrap";
import { StepProcessBox } from "./styled-components";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

import StepProcessimg from "../../assets/img/StepProcess.png";
import MobilStep from "../../assets/img/Stage.svg";

const StepProcess = () => {
  const device = useDevice(MediaQuery);
  return (
    <StepProcessBox mobile={device.isMobile}>
      <Container>
        {device.isMobile ? (
          <img src={MobilStep} alt="" />
        ) : (
          <img src={StepProcessimg} alt=""/>
        )}
      </Container>
    </StepProcessBox>
  );
};

export default StepProcess;
