import { Container } from "react-bootstrap";

import { ContainerBox, Process } from "./styled-components";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

import Processimg from "../../assets/img/Process.png";
import ProcessMpbilimg from "../../assets/img/ProcessMpbil.svg";

const Characteristics = () => {
  const device = useDevice(MediaQuery);
  return (
    <Container>
      <ContainerBox>
        <div>
          <h1> </h1>
          <p>   </p>
        </div>
        <Process mobile={device.isMobile}>
          {device.isMobile ? (
            <img src={ProcessMpbilimg} alt="" />
          ) : (
            <img src={Processimg} alt="" />
          )}
        </Process>
      </ContainerBox>
    </Container>
  );
};

export default Characteristics;
