import { ServiceBackground } from "./styled-components";
import { Container } from "react-bootstrap";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

import MobWidget from "./Components/MobWidget";
import DesktopWidget from "./Components/DesktopWidget";

const ServiceOperation = () => {
  const device = useDevice(MediaQuery);

  return (
    <ServiceBackground>
      <Container>
        <div className="Serviceh3">
          <h1>
            Service
            <br />
            Operation
          </h1>
        </div>
        {device.isMobile ? <MobWidget /> : <DesktopWidget />}
      </Container>
    </ServiceBackground>
  );
};

export default ServiceOperation;
