import ConsentForm from "./components/ConsentForm";
import {
  CallBox,
  Callinput,
  Input,
  Select,
  TextArea,
  Field,
  Button,
} from "./styled-components";

const FieldContainer = ({ children, title, required }) => (
  <Field>
    <p>
      {required && <span></span>}
      {title}
    </p>
    {children}
  </Field>
);

const Call = () => (
  <CallBox>
    <Callinput>
      <div className="Inputname">
        <FieldContainer title="" required>
          <Input />
        </FieldContainer>
        <FieldContainer title="" required>
          <Input
            onKeyPress={(e) => {
              if (
                !/[0-9]/.test(e.key) ||
                /[\u3131-\u3163\uac00-\ud7a3]/g.test(e.key)
              ) {
                e.preventDefault();
              }
            }}
          />
        </FieldContainer>
      </div>

      <FieldContainer title="" required>
        <Input />
      </FieldContainer>
      <FieldContainer title="">
        <Select>
          <option value="" disabled selected>
            
          </option>
          <option value="naverCafe"> </option>
          <option value="instagram"></option>
          <option value="onEyeRepo"></option>
          <option value="dippleWaffle"></option>
          <option value="recommendation"></option>
          <option value="youtube"></option>
          <option value="blog"></option>
          <option value="sns">SNS</option>
          <option value="search"></option>
          <option value="etc"></option>
        </Select>
      </FieldContainer>
      <FieldContainer title=" ">
        <TextArea />
      </FieldContainer>
      <ConsentForm />
      <Button></Button>
    </Callinput>
  </CallBox>
);

export default Call;
