import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";
import MobilePublic from "./components/MobilePublic";
import PublicCustomization from "./components/PublicCustomization";

const Publ = () => {
  const device = useDevice(MediaQuery);
  return (
    <div>{device.isMobile ? <MobilePublic /> : <PublicCustomization />}</div>
  );
};

export default Publ;
