import { MenuNever, Menuimg, MeunNavbar } from "./styled-components";
import { Nav, Container } from "react-bootstrap";
import { useEffect, useState } from "react";
import { Link } from "react-scroll";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";
import ilifo_logo from "../../assets/img/ilifo-logo.png";

const ScrollLink = ({ to, smooth, duration, children }) => (
  <Nav.Link as="div">
    <Link to={to} smooth={smooth} duration={duration}>
      {children}
    </Link>
  </Nav.Link>
);

const Menu = () => {
  const device = useDevice(MediaQuery);
  const [isSticky, setSticky] = useState(false);

  const checkScrollTop = () => {
    setSticky(window.scrollY >= 0);
  };

  useEffect(() => {
    window.addEventListener("scroll", checkScrollTop);
    return () => {
      window.removeEventListener("scroll", checkScrollTop);
    };
  });

  return (
    <MeunNavbar variant="light" className={isSticky ? "sticky" : ""}>
      <Container>
        <Menuimg href="#home">
          <img src={ilifo_logo} alt="" />
        </Menuimg>
        <MenuNever mobile={device.isMobile ? "true" : "false"}>
          <ScrollLink to="eduServices" smooth duration={500}>
            
          </ScrollLink>
          <ScrollLink to="sitelntr" smooth duration={500}>
            
          </ScrollLink>
          <Nav.Link href="#pricing"></Nav.Link>
        </MenuNever>
      </Container>
    </MeunNavbar>
  );
};

export default Menu;
