import Maintext from "./locale/Maintext";

import { Maintitle, MinImg } from "./styled-components";
import { Container } from "react-bootstrap";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

const Main = () => {
  const device = useDevice(MediaQuery);

  return (
    <section>
      <MinImg mobile={device.isMobile}>
        <Container>
          <Maintitle mobile={device.isMobile}>
            <Maintext />
          </Maintitle>
        </Container>
      </MinImg>
    </section>
  );
};

export default Main;
