import React, { useState, useEffect } from "react";
import { Loader } from "@googlemaps/js-api-loader";

import {
  Detailedinf,
  Directions,
  Inquiry,
  Inquirytex,
} from "../../styled-components";
import { useDevice, MediaQuery } from "../../../../Hooks/useDevice.ts";

import Companyicon from "../../../../assets/img/company.svg";
import Callicon from "../../../../assets/img/call.svg";

import ConticonBox from "../ConticonBox";
import Coninqu from "../Call/Coninqu";
import Call from "../Call/UI/Call";

const MobileConticonBox = () => {
  const device = useDevice(MediaQuery);
  const [clicked, setClicked] = useState(1);
  const [map, setMap] = useState(null);

  const handleClick = (e) => setClicked(e);

  useEffect(() => {
    if (clicked !== 1) return;
    const initMap = async () => {
      const loader = new Loader({
        apiKey: process.env.REACT_APP_GOOGLE_MAP_API_KEY,
        version: "weekly",
      });
      await loader.load();

      const map = new window.google.maps.Map(
        document.getElementById("map"),
        {
          center: { lat: 37.5793341, lng: 126.8900514 },
          zoom: 18,
        },
        []
      );

      setMap(map);
    };

    initMap();
  }, []);

  return (
    <>
      <Detailedinf mobile={device.isMobile}>
        <div>
          <div className="Button-Det">
            <Directions clicked={clicked === 2} onClick={() => handleClick(1)}>
              <div>
                <img src={Companyicon} alt="" />
                <p></p>
              </div>
            </Directions>
            <Directions clicked={clicked === 1} onClick={() => handleClick(2)}>
              <div>
                <img src={Callicon} alt="" />
                <p></p>
              </div>
            </Directions>
          </div>
        </div>
        {clicked === 1 ? (
          <div>
            <div
              id="map"
              style={{
                height: "510px",
                width: "471px",
                borderRadius: "20px",
                border: "2px solid #E4E4E4",
              }}
            />
            <div>
              <Inquiry>
                <h4></h4>
              </Inquiry>
              <Inquirytex>
                    396
                <br />
                  , 8 ()
              </Inquirytex>
              <Inquiry>
                <h4></h4>
              </Inquiry>
              <Inquirytex>smartedu@ilifo.kr / 02-6356-0182</Inquirytex>
            </div>
            {clicked === 1 ? <ConticonBox /> : <Coninqu />}
          </div>
        ) : (
          <Call />
        )}
      </Detailedinf>
    </>
  );
};

export default MobileConticonBox;
