import { Container } from "react-bootstrap";
import { Contactsection } from "./styled-components";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

import DesktopConticonBox from "./Conticon/Components/DesktopConticonBox";
import MobileConticonBox from "./Conticon/Components/MobileConticonBox";

const Contact = () => {
  const device = useDevice(MediaQuery);
  return (
    <Contactsection>
      <Container>
        <h4>Contact</h4>
        {device.isMobile ? <MobileConticonBox /> : <DesktopConticonBox />}
      </Container>
    </Contactsection>
  );
};

export default Contact;
