import { Busines } from "./styled-compomemts";
import { useDevice, MediaQuery } from "../../../../Hooks/useDevice.ts";

import React from "react";
import DesktopBusin from "./Components/DesktopBusin";
import MobileBusin from "./Components/MobBusin";

function BusinesSarea() {
  const device = useDevice(MediaQuery);
  return (
    <Busines>
      <h3>  </h3>
      {device.isMobile ? <MobileBusin /> : <DesktopBusin />}
    </Busines>
  );
}

export default BusinesSarea;
