import { Sitelntrsection } from "./styled-components";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";
import { Container } from "react-bootstrap";

import Mobilsitelntr from "./Components/Mobilsitelntr";
import Desktopsitelntr from "./Components/Desktopsitelntr";

const Sitelntr = () => {
  const device = useDevice(MediaQuery);
  return (
    <Sitelntrsection>
      <Container>
        {device.isMobile ? <Mobilsitelntr /> : <Desktopsitelntr />}
      </Container>
    </Sitelntrsection>
  );
};

export default Sitelntr;
