import { useMediaQuery } from "react-responsive";

interface DeviceSize {
  mobile: string;
  tablet: string;
  desktop: string;
}

export const MediaQuery: DeviceSize = {
  mobile: "360px",
  tablet: "760px",
  desktop: "1200px",
};

export const useDevice = (size: DeviceSize) => {
  const isMobile = useMediaQuery({
    query: `(min-width: ${size.mobile}) and (max-width: ${size.tablet})`,
  });
  const isTablet = useMediaQuery({
    query: `(min-width: ${size.tablet}) and (max-width: ${size.desktop})`,
  });
  const isDesktop = useMediaQuery({ query: `(min-width: ${size.desktop})` });
  return { isMobile, isDesktop, isTablet };
};
