import { Container } from "react-bootstrap";
import {
  Historyimgbox,
  Historybox,
  Historyyear,
  Descriptionbox,
  Month,
} from "./styled-components";
import yearEvents from "./locale/YearEvents";
import { useDevice, MediaQuery } from "../../Hooks/useDevice.ts";

const YearEvents = ({ year, events }) => {
  const device = useDevice(MediaQuery);
  return (
    <Historyyear mobile={device.isMobile}>
      <h2>{year}</h2>
      <Descriptionbox mobile={device.isMobile}>
        {events.map(({ month, description }, index) => (
          <Month
            key={`${year}-${month}-${index}`}
            aria-label={`${year} ${month}`}
          >
            <div className="blueball" />
            <h3>{month}</h3>
            <div
              className="description"
              dangerouslySetInnerHTML={{ __html: description }}
            />
          </Month>
        ))}
      </Descriptionbox>
    </Historyyear>
  );
};

const CompanyHistory = () => {
  const device = useDevice(MediaQuery);
  return (
    <section>
      <Historyimgbox mobile={device.isMobile}>
        <Container>
          <Historybox mobile={device.isMobile}>
            <h1>
              Company
              <br />
              History
            </h1>
            {yearEvents.map(({ year, events }) => (
              <YearEvents key={year} year={year} events={events} />
            ))}
          </Historybox>
        </Container>
      </Historyimgbox>
    </section>
  );
};

export default CompanyHistory;
