import { SitelntrBox, OnClickButton } from "../styled-components";
import { useDevice, MediaQuery } from "../../../Hooks/useDevice.ts";

import ilifologo from "../../../assets/img/ilifo.png";
import onilifologo from "../../../assets/img/onilifo.png";
import Diplwaplimg from "../../../assets/img/Diplwapl.png";
import onilifodef from "../../../assets/img/ondef.png";

const Mobilsitelntr = () => {
  //    onClick 
  const Diplwapl = () => {
    window.open("https://www.diplwapl.com/");
  };

  //    onClick 
  const Onilifo = () => {
    window.open("https://gbigb.onilifo.co.kr/");
  };

  const device = useDevice(MediaQuery);
  return (
    <div>
      <SitelntrBox mobile={device.isMobile}>
        <div className="Siteln">
          <div>
            <img src={Diplwaplimg} alt="" />
          </div>
          <div className="MobileSiteln">
            <img src={ilifologo} alt="" />
            <h4> </h4>
          </div>
          <p>
                 
            <br />   .   
            <br />       
            <br />
              ,  ,   <br />
               .
          </p>
          <OnClickButton variant="light" onClick={Diplwapl}>
            
          </OnClickButton>
        </div>
      </SitelntrBox>
      <hr />
      <SitelntrBox mobile={device.isMobile}>
        <div className="Siteln">
          <div>
            <img src={onilifodef} alt="" />
          </div>
          <div className="MobileSiteln">
            <img src={onilifologo} alt="" />
            <h4></h4>
          </div>
          <p>
                  <br />
               ,    <br />
                 . ,  <br />
                   <br />
                    <br />
            .
          </p>
          <OnClickButton variant="light" onClick={Onilifo}>
            
          </OnClickButton>
        </div>
      </SitelntrBox>
    </div>
  );
};

export default Mobilsitelntr;
